.footer {
  box-shadow: 0px -4px 16px 0px rgba(55, 61, 90, 0.0784313725);
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  padding: 8px 0;
}
.footer__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 28px;
}
.footer__count {
  flex-basis: 37.7%;
  background-color: var(--gray-normal);
  border: 1px solid var(--gray-regular);
  min-height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
}
.footer__btn {
  flex-basis: 61%;
}